@charset "UTF-8";
@font-face {
  font-family: 'Bebas';
  src: url('../fonts/Bebas.woff2') format('woff2'), url('../fonts/Bebas.woff') format('woff');
  font-weight: normal;
}
@font-face {
  font-family: 'Bebas-neue-bold';
  src: url('../fonts/Bebas-neue-bold.woff2') format('woff2'), url('../fonts/Bebas-neue-bold.woff') format('woff');
}
@font-face {
  font-family: 'Open-sans-bold';
  src: url('../fonts/Open-sans-bold.woff2') format('woff2'), url('../fonts/Open-sans-bold.woff') format('woff');
}
@font-face {
  font-family: 'Open-sans-light';
  src: url('../fonts/Open-sans-light.woff2') format('woff2'), url('../fonts/Open-sans-light.woff') format('woff');
}
@font-face {
  font-family: 'Open-sans';
  src: url('../fonts/Open-sans.woff2') format('woff2'), url('../fonts/Open-sans.woff') format('woff');
}
:root {
  --primary: #00A9DA;
  --secondary: #25363C;
  --accent: #F57F17;
  --error: #FF5722;
  --info: #607D8B;
  --success: #4CAF50;
  --warning: #FFC107;
  --white: #FFF;
  --black: #464646;
  --light-gray: #EFEFEF;
  --outline-focus: rgba(180, 168, 170, 0.5);
  --root-fontsize: 16rem;
  --font-family-system: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --gap: 1rem;
  --max-width-container: 1200px;
  --min-width-container: 320px;
}
html {
  height: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
  cursor: default;
  line-height: 1.15;
  padding: 0;
  text-rendering: optimizeLegibility;
  text-decoration-skip: objects;
  -moz-tab-size: 4;
  tab-size: 4;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  scroll-behavior: smooth;
}
body {
  margin: 0;
}
button {
  border: 0;
}
button:active,
button:focus,
.currency:active,
.currency:focus {
  outline: none;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  /*max-width: 100%;*/
  /*visibility: hidden;*/
}
.ibg img {
  visibility: hidden;
}
.icon-logo {
  width: 12px;
}
.wrapper {
  max-width: 1140px;
  margin: 0 auto;
}
header .wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 15px;
  padding-bottom: 20px;
}
.header_nav {
  display: flex;
  align-items: flex-end;
  margin-bottom: 20px;
}
.header_nav ul {
  display: flex;
}
.header_nav li {
  font-family: Bebas;
  font-size: 16px;
  text-transform: uppercase;
  margin-left: 42px;
}
.header_nav li:hover {
  color: #4e26fd;
}
.logo {
  display: flex;
  flex-direction: column;
  position: relative;
}
.hotel {
  font-family: Bebas-neue-bold;
  text-transform: uppercase;
  font-size: 30px;
}
.name {
  font-family: Bebas-neue-bold;
  text-transform: uppercase;
  font-size: 40.5px;
  margin-top: -10px;
  letter-spacing: 1px;
  color: #4e26fd;
  font-weight: bold;
}
.topstar,
.leftstar,
.rightstar {
  position: absolute;
  fill: #4e26fd;
}
.topstar {
  top: -102%;
  right: 10%;
}
.leftstar {
  top: -82%;
  right: 18%;
}
.rightstar {
  top: -82%;
  right: 0%;
}
.main_photo {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  font-size: 0;
  width: 100%;
  position: relative;
}
.left_column {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 70%;
  flex: 0 0 70%;
}
.right_column {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 30%;
  flex: 0 0 30%;
}
.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
/*.main_photo_img{
  height: 100%;
}*/
.perfection {
  font-family: Bebas-neue-bold;
  text-transform: uppercase;
  font-size: 111.71px;
  position: absolute;
  color: #EFEFEF;
  bottom: 5px;
  left: 10%;
  transform: translate(0px 0px);
  opacity: 0;
  transition: all 0.8s ease 0s;
}
.invited {
  font-family: Bebas-neue-bold;
  text-transform: uppercase;
  font-size: 70px;
  position: absolute;
  color: black;
  bottom: -55px;
  left: 10%;
  transform: translate(0px, 0px);
  opacity: 0;
  transition: all 0.8s ease 0s;
}
.perfection._active {
  transform: translate(0px 0px);
  opacity: 1;
}
.invited._active {
  transform: translate(0px, 0px);
  opacity: 1;
  transition: all 0.8s ease 0.7s;
}
figure a {
  transform: scale(0);
  transition: all 2s ease 0s;
}
figure a._active {
  transform: scale(1);
}
/*.main_photo_img:first-child {
  display: none;
}*/
.wrapper .booking {
  margin-top: 55px;
  padding: 35px 5px;
  background-color: black;
  flex-wrap: nowrap;
  border-bottom: 10px solid #4e26fd;
  margin-bottom: 20px;
}
.client_info {
  display: flex;
  justify-content: center;
  align-items: center;
}
.client_info input {
  margin: 0 7px;
  width: 18%;
  font-family: Open-sans;
  font-size: 17px;
  padding: 10px 5px;
  border: none;
}
.client_info button {
  width: 17%;
  font-family: Bebas;
  font-size: 20px;
  text-transform: uppercase;
  color: #efefef;
  background-color: #4e26fd;
  padding: 10px 5px;
  border: none;
}
.descript-title {
  font-family: Bebas-neue-bold;
  font-size: 55px;
  text-transform: uppercase;
  color: black;
  text-align: center;
  margin: 80px 0 20px 0;
  letter-spacing: 4px;
}
.hotel-description {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.descript_items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.descript_items figure {
  width: calc(100%/3 - 10px);
  margin: 5px 5px 7px 5px;
}
.descript_items a {
  display: block;
  padding: 0 0 1% 0;
}
.description-details {
  font-family: Open-sans;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  letter-spacing: 1px;
}
.details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 32px 0 10px 0;
  transform: translate(0px, 50%);
  opacity: 0;
  transition: all 0.3s ease 0s;
}
.details._active {
  transform: translate(0px, 0px);
  opacity: 1;
  transition: all 0.8s ease 0s;
}
.details p {
  text-align: center;
  font-family: Open-sans-light;
  font-size: 16px;
}
.additional_info {
  color: #4e26fd;
  border: 3px solid #4e26fd;
  padding: 10px 5px;
  font-family: Bebas;
  font-size: 24px;
  width: 150px;
  background-color: white;
  text-align: center;
  margin-top: 4px;
}
.additional_info:hover {
  color: white;
  background-color: #4e26fd;
}
.wa-pictures {
  display: flex;
  margin-top: 20px;
  width: 100%;
}
.wa-title-top {
  font-family: Bebas-neue-bold;
  font-size: 80px;
  text-align: center;
  margin-top: 40px;
  letter-spacing: 3px;
}
.winter_act {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}
.wa-pics {
  position: relative;
  display: block;
  width: 100%;
}
.wa-descript {
  position: absolute;
  z-index: 5;
  left: 11%;
  bottom: 6%;
}
.wa-about {
  text-align: center;
  font-family: Open-sans-light;
  font-size: 16px;
  color: #efefef;
  letter-spacing: 1px;
}
.wa-title {
  font-family: Bebas-neue-bold;
  font-size: 50px;
  color: #efefef;
  text-transform: uppercase;
  margin-bottom: 5px;
}
.deluxe-rooms-items {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -5px;
  margin-bottom: 10px;
  flex-wrap: wrap;
  /* add */
}
.dr-column-doub,
.delrooms-centre {
  display: flex;
  flex-direction: column;
}
.delrooms-centre {
  width: 60%;
}
.dr-column-doub {
  width: 20%;
  /* before 22% */
}
.delroom-details {
  margin-top: 10px;
}
.delroom-details span {
  font-family: Open-sans-bold;
  font-size: 16px;
}
.delroom-details p {
  font-family: Open-sans-light;
  font-size: 16px;
}
.dr-column-doub figure {
  margin: 0;
}
.delroom-title {
  font-family: Bebas-neue-bold;
  font-size: 80px;
  text-align: center;
  margin: 85px 10px 50px 10px;
  letter-spacing: 4px;
}
.deluxe-rooms-section,
.news {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 65px;
}
.view,
.view-centre {
  display: block;
  padding: 0 0 0 0;
}
.winter-weath {
  /*background-image: url("../../assets/images/winter-bcg.png");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: 100% auto;
  min-height: 540px;*/
  position: relative;
  margin-top: 90px;
}
.ww-text {
  position: absolute;
  left: 55%;
  top: 18%;
  z-index: 5;
}
.ww-text span {
  font-family: Bebas-neue-bold;
  font-size: 130px;
  color: #efefef;
  text-transform: uppercase;
  line-height: 110px;
  letter-spacing: 5px;
}
.ww-text p {
  margin-top: 17px;
  font-family: Open-sans-light;
  font-size: 16px;
  color: #efefef;
  width: 70%;
  line-height: 24px;
  padding-left: 15px;
}
.ww-additional_info {
  color: white;
  border: 3px solid #4e26fd;
  padding: 15px 5px;
  font-family: Bebas;
  font-size: 17px;
  width: 160px;
  background-color: #0000ff;
  text-align: center;
  margin-top: 40px;
  margin-left: 15px;
}
.news-section {
  display: flex;
  justify-content: center;
  margin: 15px 0 15px 0;
  flex-wrap: wrap;
  /* add */
}
.news-section figure {
  margin: 5px 20px 10px 20px;
  /* before 5px */
  /* padding: 10px; */
  width: calc(100%/3 - 40px);
  /* add */
}
.news-us {
  display: block;
  padding: 0 0 1% 0;
}
.news-details {
  margin-top: 25px;
}
/*.news-section img{
  margin-bottom: 25px;
}*/
.news-title {
  font-family: Bebas-neue-bold;
  font-size: 111px;
  margin: 80px 0 45px 0;
  letter-spacing: 5px;
}
.news-description {
  font-family: Open-sans-bold;
  font-size: 16px;
}
.news-details p {
  font-family: Open-sans-light;
  font-size: 16px;
  line-height: 24px;
}
.footer {
  background-color: #252525;
  padding: 40px 20px;
}
.footer .logo {
  max-width: 115px;
}
.footer .hotel {
  color: white;
}
.footer-details {
  display: flex;
  margin-bottom: 15px;
  padding-bottom: 40px;
  border-bottom: 2px solid #303030;
}
.column1 {
  display: flex;
  flex-direction: column;
  flex: 0 0 30%;
  margin-left: 10px;
  margin-top: 10px;
}
.footer-icons {
  width: 11px;
  height: 16px;
  fill: white;
  margin-right: 7px;
}
.footer-icons:hover {
