html {
  height: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
  cursor: default;
  line-height: 1.15;
  padding: 0;
  text-rendering: optimizeLegibility;
  text-decoration-skip: objects;
  -moz-tab-size: 4;
  tab-size: 4;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  scroll-behavior: smooth;
}
body{
  margin: 0;
}
button{
  border: 0;
}

button:active, button:focus, .currency:active, .currency:focus{
  outline: none;
}

ul{
  margin: 0;
  padding: 0;
}

li{
  list-style-type: none;
}

a{
  text-decoration: none;
  color: inherit;
}

img{
  /*max-width: 100%;*/
  /*visibility: hidden;*/
}
.ibg img{
  visibility: hidden;
}

.icon-logo{
  width: 12px;
}
.wrapper{
  max-width: 1140px;
  margin: 0 auto;
}
header .wrapper{
  display: flex;
  justify-content: space-between;
  padding-top: 15px;
  padding-bottom: 20px;
}
.header_nav {
  display: flex;
  align-items: flex-end;
  margin-bottom: 20px;
}
.header_nav ul{
  display: flex;
}
.header_nav li{
  font-family: Bebas;
  font-size: 16px;
  text-transform: uppercase;
  margin-left: 42px;
}
.header_nav li:hover{
  color: #4e26fd;
}
.logo{
  display: flex;
  flex-direction: column;
  position: relative;
}
.hotel{
  font-family: Bebas-neue-bold;
  text-transform: uppercase;
  font-size: 30px;
}
.name{
  font-family: Bebas-neue-bold;
  text-transform: uppercase;
  font-size: 40.5px;
  margin-top: -10px;
  letter-spacing: 1px;

  color: #4e26fd;
  font-weight: bold;
}
.topstar, .leftstar, .rightstar{
  position: absolute;
  fill: #4e26fd;
}
.topstar{
  top: -102%;
  right: 10%;
}
.leftstar{
  top: -82%;
  right: 18%;
}
.rightstar{
  top: -82%;
  right: 0%;
}
.main_photo{
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  font-size: 0;
  width: 100%;
  position: relative;
}
.left_column{
  -webkit-box-flex: 0;
  -ms-flex: 0 0 70%;
  flex: 0 0 70%;
}

.right_column{
  -webkit-box-flex: 0;
  -ms-flex: 0 0 30%;
  flex: 0 0 30%;
}
.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
/*.main_photo_img{
  height: 100%;
}*/

.perfection{
  font-family: Bebas-neue-bold;
  text-transform: uppercase;
  font-size: 111.71px;
  position: absolute;
  color: #EFEFEF;
  bottom: 5px;
  left: 10%;

  transform: translate(0px 0px);
  opacity: 0;
  transition: all 0.8s ease 0s;
}
.invited{
  font-family: Bebas-neue-bold;
  text-transform: uppercase;
  font-size: 70px;
  position: absolute;
  color: black;
  bottom: -55px;
  left: 10%;

  transform: translate(0px, 0px);
  opacity: 0;
  transition: all 0.8s ease 0s;
}
.perfection._active{
  transform: translate(0px 0px);
  opacity: 1;
}
.invited._active{
  transform: translate(0px, 0px);
  opacity: 1;
  transition: all 0.8s ease 0.7s;
}
figure a{
  transform: scale(0);
  transition: all 2.0s ease 0s;
}
figure a._active{
  transform: scale(1);
}
/*.main_photo_img:first-child {
  display: none;
}*/
.wrapper .booking{
  margin-top: 55px;
  padding: 35px 5px;
  background-color: black;
  flex-wrap: nowrap;
  border-bottom: 10px solid #4e26fd;
  margin-bottom: 20px;
}
.client_info {
  display: flex;
  justify-content: center;
  align-items: center;
}
.client_info input{
  margin: 0 7px;
  width: 18%;
  font-family: Open-sans;
  font-size: 17px;
  padding: 10px 5px;
  border: none;
}
.client_info button{
