}
@media screen and (max-width: 800px) {
  .header_nav li {
    margin-left: 20px;
  }
  .winter_act {
    display: none;
  }
  .delrooms-centre {
    width: 85%;
    order: 2;
  }
  .dr-column-doub {
    width: 35%;
    margin: 5px 15px;
  }
  .client_info button {
    font-size: 16px;
  }
  .ww-text {
    left: 40%;
  }
  small{
    margin-left: 0;
  }
  .right_column {
    display: none;
  }
}

@media screen and (max-width: 650px) {
  .footer-details{
    flex-wrap: wrap;
  }
  .column3{
    order: 1;
    margin-top: 40px;
  }
  .footer-link a {
    margin: 5px 40px 5px 0;
  }
  .column2{
    padding-left: 90px;
  }
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 30;
    margin-top: 30px;
    margin-left: 5px;
  }
  .header__burger span{
    position: absolute;
    background-color: black;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before,
  .header__burger:after{
    content: '';
    background-color: black;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before{
    top: 0;
  }
  .header__burger:after{
    bottom: 0;
  }
  .header__burger.active span{
    transform: scale(0);
  }

  .header__burger.active:before{
    transform:rotate(45deg);
    top: 9px;
  }
  .header__burger.active:after{
    transform:rotate(-45deg);
    bottom: 9px;
  }
  .header_nav{
    position: absolute;
    top: -200%;
    left: 70%;
    width: 100%;
    height: 100%;
    overflow: auto;
    transition: all 0.3s ease 0s;
    padding: 260px 10px 20px 10px;
    z-index: 5;
    overflow: auto;
  }
  .header_nav.active{
    top:-85%;
  }
  .header_nav ul {
    display: block;
  }
  .header_nav li {
    margin-bottom: 20px;
    font-size: 18px;
    color: white;
    margin-right: 25px;
  }
  .logo {
    display: flex;
    z-index: 5;
  }
  header .wrapper {
    padding: 10px 20px;
  }
  .client_info button {
    font-size: 14px;
    padding: 12px 5px;
  }
}
@media screen and (max-width: 600px) {
  .descript_items figure {
    width: calc(100%/2 - 20px);
    margin: 5px 10px;
  }
  .winter-weath, .news {
    display: none;
  }
  .deluxe-rooms-section {
    margin-bottom: 15px;
  }
  .news-section figure {
    margin: 5px 10px;
    width: 90%;
  }
  .delroom-title{
    font-size: 65px;
  }
  .delroom-title{
    letter-spacing: 0;
  }
}
@media screen and (max-width: 550px) {
  .perfection {
    font-size: 80px;
  }
  .invited{
    font-size: 55px;
    bottom: -7%;
  }
  .client_info{
    flex-wrap: wrap;
  }
  .client_info button{
    margin-top: 15px;
    width: 30%;
  }
  .wrapper .booking {
    padding: 17px 5px;
    margin-top: 20px;
  }
  .descript-title{
    font-size: 45px;
    margin-top: 15px;
  }
  .left_column{
    overflow: hidden;
  }
}
@media screen and (max-width: 500px) {
  .dr-column-doub{
    width: 60%;
  }
  .delrooms-centre{
